	module.exports = {"data":{"postsJson":{"title":"The Sinful City is Fallen","questionsTitle":"COMPARISON, CONTRAST & REPETITION","display":"block","bookName":"Revelation - ΑΠΟΚΑΛΥΨΙΣ ΙΩΑΝΝΟΥ","ref":"17:1-20:3","html":"<p>   <strong>17</strong><u>1</u>Καὶ ἦλθεν εἷς ἐκ τῶν ἑπτὰ ἀγγέλων τῶν ἐχόντων τὰς ἑπτὰ φιάλας καὶ ἐλάλησεν μετʼ ἐμοῦ λέγων·</p><h1>ONE OF THE SEVEN ANGELS</h1><h4>δεῦρο, δείξω σοι τὸ κρίμα τῆς πόρνης τῆς μεγάλης τῆς καθημένης ἐπὶ ὑδάτων πολλῶν, <u>2</u>μεθʼ ἧς ἐπόρνευσαν οἱ βασιλεῖς τῆς γῆς καὶ ἐμεθύσθησαν οἱ κατοικοῦντες τὴν γῆν ἐκ τοῦ οἴνου τῆς πορνείας αὐτῆς.</h4><p><u>3</u>καὶ ἀπήνεγκέν με εἰς ἔρημον ἐν πνεύματι.<br />   Καὶ εἶδον γυναῖκα καθημένην ἐπὶ θηρίον κόκκινον, γέμοντα ὀνόματα βλασφημίας, ἔχων κεφαλὰς ἑπτὰ καὶ κέρατα δέκα. <u>4</u>καὶ ἡ γυνὴ ἦν περιβεβλημένη πορφυροῦν καὶ κόκκινον καὶ κεχρυσωμένη χρυσίῳ καὶ λίθῳ τιμίῳ καὶ μαργαρίταις, ἔχουσα ποτήριον χρυσοῦν ἐν τῇ χειρὶ αὐτῆς γέμον βδελυγμάτων καὶ τὰ ἀκάθαρτα τῆς πορνείας αὐτῆς <u>5</u>καὶ ἐπὶ τὸ μέτωπον αὐτῆς ὄνομα γεγραμμένον, μυστήριον, Βαβυλὼν ἡ μεγάλη, ἡ μήτηρ τῶν πορνῶν καὶ τῶν βδελυγμάτων τῆς γῆς. <u>6</u>καὶ εἶδον τὴν γυναῖκα μεθύουσαν ἐκ τοῦ αἵματος τῶν ἁγίων καὶ ἐκ τοῦ αἵματος τῶν μαρτύρων Ἰησοῦ. Καὶ ἐθαύμασα ἰδὼν αὐτὴν θαῦμα μέγα.<br />   <u>7</u>Καὶ εἶπέν μοι ὁ ἄγγελος·</p><h1>ONE OF THE SEVEN ANGELS</h1><h4>διὰ τί ἐθαύμασας; ἐγὼ ἐρῶ σοι τὸ μυστήριον τῆς γυναικὸς καὶ τοῦ θηρίου τοῦ βαστάζοντος αὐτὴν τοῦ ἔχοντος τὰς ἑπτὰ κεφαλὰς καὶ τὰ δέκα κέρατα.<br /><u>8</u>Τὸ θηρίον ὃ εἶδες ἦν καὶ οὐκ ἔστιν καὶ μέλλει ἀναβαίνειν ἐκ τῆς ἀβύσσου καὶ εἰς ἀπώλειαν ὑπάγει, καὶ θαυμασθήσονται οἱ κατοικοῦντες ἐπὶ τῆς γῆς, ὧν οὐ γέγραπται τὸ ὄνομα ἐπὶ τὸ βιβλίον τῆς ζωῆς ἀπὸ καταβολῆς κόσμου, βλεπόντων τὸ θηρίον ὅτι ἦν καὶ οὐκ ἔστιν καὶ παρέσται. <u>9</u>ὧδε ὁ νοῦς ὁ ἔχων σοφίαν. Αἱ ἑπτὰ κεφαλαὶ ἑπτὰ ὄρη εἰσίν, ὅπου ἡ γυνὴ κάθηται ἐπʼ αὐτῶν. καὶ βασιλεῖς ἑπτά εἰσιν· <u>10</u>οἱ πέντε ἔπεσαν, ὁ εἷς ἔστιν, ὁ ἄλλος οὔπω ἦλθεν, καὶ ὅταν ἔλθῃ ὀλίγον αὐτὸν δεῖ μεῖναι. <u>11</u>καὶ τὸ θηρίον ὃ ἦν καὶ οὐκ ἔστιν καὶ αὐτὸς ὄγδοός ἐστιν καὶ ἐκ τῶν ἑπτά ἐστιν, καὶ εἰς ἀπώλειαν ὑπάγει. <u>12</u>Καὶ τὰ δέκα κέρατα ἃ εἶδες δέκα βασιλεῖς εἰσιν, οἵτινες βασιλείαν οὔπω ἔλαβον, ἀλλʼ ἐξουσίαν ὡς βασιλεῖς μίαν ὥραν λαμβάνουσιν μετὰ τοῦ θηρίου. <u>13</u>οὗτοι μίαν γνώμην ἔχουσιν καὶ τὴν δύναμιν καὶ ἐξουσίαν αὐτῶν τῷ θηρίῳ διδόασιν. <u>14</u>οὗτοι μετὰ τοῦ ἀρνίου πολεμήσουσιν καὶ τὸ ἀρνίον νικήσει αὐτούς, ὅτι κύριος κυρίων ἐστὶν καὶ βασιλεὺς βασιλέων καὶ οἱ μετʼ αὐτοῦ κλητοὶ καὶ ἐκλεκτοὶ καὶ πιστοί.</h4><p>   <u>15</u>Καὶ λέγει μοι·</p><h1>ONE OF THE SEVEN ANGELS</h1><h4>τὰ ὕδατα ἃ εἶδες οὗ ἡ πόρνη κάθηται, λαοὶ καὶ ὄχλοι εἰσὶν καὶ ἔθνη καὶ γλῶσσαι. <u>16</u>καὶ τὰ δέκα κέρατα ἃ εἶδες καὶ τὸ θηρίον οὗτοι μισήσουσιν τὴν πόρνην καὶ ἠρημωμένην ποιήσουσιν αὐτὴν καὶ γυμνὴν καὶ τὰς σάρκας αὐτῆς φάγονται καὶ αὐτὴν κατακαύσουσιν ἐν πυρί. <u>17</u>ὁ γὰρ θεὸς ἔδωκεν εἰς τὰς καρδίας αὐτῶν ποιῆσαι τὴν γνώμην αὐτοῦ καὶ ποιῆσαι μίαν γνώμην καὶ δοῦναι τὴν βασιλείαν αὐτῶν τῷ θηρίῳ ἄχρι τελεσθήσονται οἱ λόγοι τοῦ θεοῦ. <u>18</u>καὶ ἡ γυνὴ ἣν εἶδες ἔστιν ἡ πόλις ἡ μεγάλη ἡ ἔχουσα βασιλείαν ἐπὶ τῶν βασιλέων τῆς γῆς.</h4><p>   <strong>18</strong><u>1</u>Μετὰ ταῦτα εἶδον ἄλλον ἄγγελον καταβαίνοντα ἐκ τοῦ οὐρανοῦ ἔχοντα ἐξουσίαν μεγάλην, καὶ ἡ γῆ ἐφωτίσθη ἐκ τῆς δόξης αὐτοῦ. <u>2</u>καὶ ἔκραξεν ἐν ἰσχυρᾷ φωνῇ λέγων·</p><h1>AN ANGEL WITH AUTHORITY</h1><h4>ἔπεσεν ἔπεσεν Βαβυλὼν ἡ μεγάλη, καὶ ἐγένετο κατοικητήριον δαιμονίων καὶ φυλακὴ παντὸς πνεύματος ἀκαθάρτου καὶ φυλακὴ παντὸς ὀρνέου ἀκαθάρτου καὶ φυλακὴ παντὸς θηρίου ἀκαθάρτου καὶ μεμισημένου,<br />   <u>3</u>ὅτι ἐκ τοῦ οἴνου τοῦ θυμοῦ τῆς πορνείας αὐτῆς πέπωκαν πάντα τὰ ἔθνη καὶ οἱ βασιλεῖς τῆς γῆς μετʼ αὐτῆς ἐπόρνευσαν καὶ οἱ ἔμποροι τῆς γῆς ἐκ τῆς δυνάμεως τοῦ στρήνους αὐτῆς ἐπλούτησαν.</h4><p><u>4</u>Καὶ ἤκουσα ἄλλην φωνὴν ἐκ τοῦ οὐρανοῦ λέγουσαν·</p><h1>GOD</h1><h2>ἐξέλθατε ὁ λαός μου ἐξ αὐτῆς ἵνα μὴ συγκοινωνήσητε ταῖς ἁμαρτίαις αὐτῆς, καὶ ἐκ τῶν πληγῶν αὐτῆς ἵνα μὴ λάβητε,<br />   <u>5</u>ὅτι ἐκολλήθησαν αὐτῆς αἱ ἁμαρτίαι ἄχρι τοῦ οὐρανοῦ καὶ ἐμνημόνευσεν ὁ θεὸς τὰ ἀδικήματα αὐτῆς. <u>6</u>ἀπόδοτε αὐτῇ ὡς καὶ αὐτὴ ἀπέδωκεν καὶ διπλώσατε τὰ διπλᾶ κατὰ τὰ ἔργα αὐτῆς, ἐν τῷ ποτηρίῳ ᾧ ἐκέρασεν κεράσατε αὐτῇ διπλοῦν, <u>7</u>ὅσα ἐδόξασεν αὐτὴν καὶ ἐστρηνίασεν, τοσοῦτον δότε αὐτῇ βασανισμὸν καὶ πένθος. ὅτι ἐν τῇ καρδίᾳ αὐτῆς λέγει ὅτι κάθημαι βασίλισσα καὶ χήρα οὐκ εἰμὶ καὶ πένθος οὐ μὴ ἴδω. <u>8</u>διὰ τοῦτο ἐν μιᾷ ἡμέρᾳ ἥξουσιν αἱ πληγαὶ αὐτῆς, θάνατος καὶ πένθος καὶ λιμός, καὶ ἐν πυρὶ κατακαυθήσεται, ὅτι ἰσχυρὸς κύριος ὁ θεὸς ὁ κρίνας αὐτήν.</h2><p><u>9</u>Καὶ κλαύσουσιν καὶ κόψονται ἐπʼ αὐτὴν οἱ βασιλεῖς τῆς γῆς οἱ μετʼ αὐτῆς πορνεύσαντες καὶ στρηνιάσαντες, ὅταν βλέπωσιν τὸν καπνὸν τῆς πυρώσεως αὐτῆς, <u>10</u>ἀπὸ μακρόθεν ἑστηκότες διὰ τὸν φόβον τοῦ βασανισμοῦ αὐτῆς λέγοντες·</p><h1>THE WORLD’S RULERS</h1><h4>οὐαὶ οὐαί, ἡ πόλις ἡ μεγάλη,<br />   Βαβυλὼν ἡ πόλις ἡ ἰσχυρά,<br />   ὅτι μιᾷ ὥρᾳ ἦλθεν ἡ κρίσις σου.</h4><p><u>11</u>Καὶ οἱ ἔμποροι τῆς γῆς κλαίουσιν καὶ πενθοῦσιν ἐπʼ αὐτήν, ὅτι τὸν γόμον αὐτῶν οὐδεὶς ἀγοράζει οὐκέτι <u>12</u>γόμον χρυσοῦ καὶ ἀργύρου καὶ λίθου τιμίου καὶ μαργαριτῶν καὶ βυσσίνου καὶ πορφύρας καὶ σιρικοῦ καὶ κοκκίνου, καὶ πᾶν ξύλον θύϊνον καὶ πᾶν σκεῦος ἐλεφάντινον καὶ πᾶν σκεῦος ἐκ ξύλου τιμιωτάτου καὶ χαλκοῦ καὶ σιδήρου καὶ μαρμάρου, <u>13</u>καὶ κιννάμωμον καὶ ἄμωμον καὶ θυμιάματα καὶ μύρον καὶ λίβανον καὶ οἶνον καὶ ἔλαιον καὶ σεμίδαλιν καὶ σῖτον καὶ κτήνη καὶ πρόβατα, καὶ ἵππων καὶ ῥεδῶν καὶ σωμάτων, καὶ ψυχὰς ἀνθρώπων.</p><h1>THE WORLD’S MERCHANTS</h1><h4><u>14</u>καὶ ἡ ὀπώρα σου τῆς ἐπιθυμίας τῆς ψυχῆς ἀπῆλθεν ἀπὸ σοῦ,<br />   καὶ πάντα τὰ λιπαρὰ καὶ τὰ λαμπρὰ ἀπώλετο ἀπὸ σοῦ<br />   καὶ οὐκέτι οὐ μὴ αὐτὰ εὑρήσουσιν.</h4><p>   <u>15</u>Οἱ ἔμποροι τούτων οἱ πλουτήσαντες ἀπʼ αὐτῆς ἀπὸ μακρόθεν στήσονται διὰ τὸν φόβον τοῦ βασανισμοῦ αὐτῆς κλαίοντες καὶ πενθοῦντες <u>16</u>λέγοντες·</p><h1>THE WORLD’S MERCHANTS</h1><h4>οὐαὶ οὐαί, ἡ πόλις ἡ μεγάλη,<br />   ἡ περιβεβλημένη βύσσινον καὶ πορφυροῦν καὶ κόκκινον<br />   καὶ κεχρυσωμένη ἐν χρυσίῳ καὶ λίθῳ τιμίῳ καὶ μαργαρίτῃ,<br />   <u>17</u>ὅτι μιᾷ ὥρᾳ ἠρημώθη ὁ τοσοῦτος πλοῦτος.</h4><p>Καὶ πᾶς κυβερνήτης καὶ πᾶς ὁ ἐπὶ τόπον πλέων καὶ ναῦται καὶ ὅσοι τὴν θάλασσαν ἐργάζονται, ἀπὸ μακρόθεν ἔστησαν <u>18</u>καὶ ἔκραζον βλέποντες τὸν καπνὸν τῆς πυρώσεως αὐτῆς λέγοντες·</p><h1>THE WORLD’S SHIPPERS</h1><h4>τίς ὁμοία τῇ πόλει τῇ μεγάλῃ;</h4><p><u>19</u>καὶ ἔβαλον χοῦν ἐπὶ τὰς κεφαλὰς αὐτῶν καὶ ἔκραζον κλαίοντες καὶ πενθοῦντες λέγοντες·</p><h1>THE WORLD’S SHIPPERS</h1><h4>οὐαὶ οὐαί, ἡ πόλις ἡ μεγάλη,<br />   ἐν ᾗ ἐπλούτησαν πάντες οἱ ἔχοντες τὰ πλοῖα ἐν τῇ θαλάσσῃ ἐκ τῆς τιμιότητος αὐτῆς,<br />   ὅτι μιᾷ ὥρᾳ ἠρημώθη.</h4><p>   <u>20</u>Εὐφραίνου ἐπʼ αὐτῇ, οὐρανὲ<br />   καὶ οἱ ἅγιοι καὶ οἱ ἀπόστολοι καὶ οἱ προφῆται,<br />   ὅτι ἔκρινεν ὁ θεὸς τὸ κρίμα ὑμῶν ἐξ αὐτῆς.<br />   <u>21</u>Καὶ ἦρεν εἷς ἄγγελος ἰσχυρὸς λίθον ὡς μύλινον μέγαν καὶ ἔβαλεν εἰς τὴν θάλασσαν λέγων·</p><h1>AN ANGEL WITH A BOULDER</h1><h4>οὕτως ὁρμήματι βληθήσεται Βαβυλὼν ἡ μεγάλη πόλις<br />   καὶ οὐ μὴ εὑρεθῇ ἔτι.<br />   <u>22</u>καὶ φωνὴ κιθαρῳδῶν καὶ μουσικῶν καὶ αὐλητῶν καὶ σαλπιστῶν<br />   οὐ μὴ ἀκουσθῇ ἐν σοὶ ἔτι,<br />   καὶ πᾶς τεχνίτης πάσης τέχνης<br />   οὐ μὴ εὑρεθῇ ἐν σοὶ ἔτι,<br />   καὶ φωνὴ μύλου<br />   οὐ μὴ ἀκουσθῇ ἐν σοὶ ἔτι,<br />   <u>23</u>καὶ φῶς λύχνου<br />   οὐ μὴ φάνῃ ἐν σοὶ ἔτι,<br />   καὶ φωνὴ νυμφίου καὶ νύμφης<br />   οὐ μὴ ἀκουσθῇ ἐν σοὶ ἔτι·<br />   ὅτι οἱ ἔμποροί σου ἦσαν οἱ μεγιστᾶνες τῆς γῆς,<br />   ὅτι ἐν τῇ φαρμακείᾳ σου ἐπλανήθησαν πάντα τὰ ἔθνη,<br />   <u>24</u>καὶ ἐν αὐτῇ αἷμα προφητῶν καὶ ἁγίων εὑρέθη<br />   καὶ πάντων τῶν ἐσφαγμένων ἐπὶ τῆς γῆς.</h4><p>   <strong>19</strong><u>1</u>Μετὰ ταῦτα ἤκουσα ὡς φωνὴν μεγάλην ὄχλου πολλοῦ ἐν τῷ οὐρανῷ λεγόντων·</p><h1>A VAST HEAVENLY CROWD</h1><h4>ἁλληλουϊά·<br />   ἡ σωτηρία καὶ ἡ δόξα καὶ ἡ δύναμις τοῦ θεοῦ ἡμῶν,<br />   <u>2</u>ὅτι ἀληθιναὶ καὶ δίκαιαι αἱ κρίσεις αὐτοῦ·<br />   ὅτι ἔκρινεν τὴν πόρνην τὴν μεγάλην<br />   ἥτις ἔφθειρεν τὴν γῆν ἐν τῇ πορνείᾳ αὐτῆς,<br />   καὶ ἐξεδίκησεν τὸ αἷμα τῶν δούλων αὐτοῦ ἐκ χειρὸς αὐτῆς.</h4><p><u>3</u>Καὶ δεύτερον εἴρηκαν·</p><h1>A VAST HEAVENLY CROWD</h1><h4>ἁλληλουϊά·<br />   καὶ ὁ καπνὸς αὐτῆς ἀναβαίνει εἰς τοὺς αἰῶνας τῶν αἰώνων.</h4><p><u>4</u>καὶ ἔπεσαν οἱ πρεσβύτεροι οἱ εἴκοσι τέσσαρες καὶ τὰ τέσσαρα ζῷα καὶ προσεκύνησαν τῷ θεῷ τῷ καθημένῳ ἐπὶ τῷ θρόνῳ λέγοντες·</p><h1>FOUR LIVING BEINGS AND 24 ELDERS</h1><h4>ἀμὴν ἁλληλουϊά.</h4><p><u>5</u>Καὶ φωνὴ ἀπὸ τοῦ θρόνου ἐξῆλθεν λέγουσα·</p><h1>GOD</h1><h2>αἰνεῖτε τῷ θεῷ ἡμῶν<br />   πάντες οἱ δοῦλοι αὐτοῦ<br />   καὶ οἱ φοβούμενοι αὐτόν,<br />   οἱ μικροὶ καὶ οἱ μεγάλοι.</h2><p><u>6</u>Καὶ ἤκουσα ὡς φωνὴν ὄχλου πολλοῦ καὶ ὡς φωνὴν ὑδάτων πολλῶν καὶ ὡς φωνὴν βροντῶν ἰσχυρῶν λεγόντων·</p><h1>A VAST HEAVENLY CROWD</h1><h4>ἁλληλουϊά,<br />   ὅτι ἐβασίλευσεν κύριος ὁ θεὸς ἡμῶν ὁ παντοκράτωρ.<br />   <u>7</u>χαίρωμεν καὶ ἀγαλλιῶμεν καὶ δώσωμεν τὴν δόξαν αὐτῷ,<br />   ὅτι ἦλθεν ὁ γάμος τοῦ ἀρνίου καὶ ἡ γυνὴ αὐτοῦ ἡτοίμασεν ἑαυτὴν<br />   <u>8</u>καὶ ἐδόθη αὐτῇ ἵνα περιβάληται βύσσινον λαμπρὸν καθαρόν·</h4><p>τὸ γὰρ βύσσινον τὰ δικαιώματα τῶν ἁγίων ἐστίν.<br />   <u>9</u>Καὶ λέγει μοι·</p><h1>A GUIDING ANGEL</h1><h4>γράψον· μακάριοι οἱ εἰς τὸ δεῖπνον τοῦ γάμου τοῦ ἀρνίου κεκλημένοι.</h4><p>καὶ λέγει μοι·</p><h1>A GUIDING ANGEL</h1><h4>οὗτοι οἱ λόγοι ἀληθινοὶ τοῦ θεοῦ εἰσιν. <u>10</u>καὶ ἔπεσα ἔμπροσθεν τῶν ποδῶν αὐτοῦ προσκυνῆσαι αὐτῷ. καὶ λέγει μοι· ὅρα μή· σύνδουλός σού εἰμι καὶ τῶν ἀδελφῶν σου τῶν ἐχόντων τὴν μαρτυρίαν Ἰησοῦ· τῷ θεῷ προσκύνησον. ἡ γὰρ μαρτυρία Ἰησοῦ ἐστιν τὸ πνεῦμα τῆς προφητείας.</h4><p>   <u>11</u>Καὶ εἶδον τὸν οὐρανὸν ἠνεῳγμένον, καὶ ἰδοὺ ἵππος λευκὸς καὶ ὁ καθήμενος ἐπʼ αὐτὸν καλούμενος πιστὸς καὶ ἀληθινός, καὶ ἐν δικαιοσύνῃ κρίνει καὶ πολεμεῖ. <u>12</u>οἱ δὲ ὀφθαλμοὶ αὐτοῦ ὡς φλὸξ πυρός, καὶ ἐπὶ τὴν κεφαλὴν αὐτοῦ διαδήματα πολλά, ἔχων ὄνομα γεγραμμένον ὃ οὐδεὶς οἶδεν εἰ μὴ αὐτός, <u>13</u>καὶ περιβεβλημένος ἱμάτιον βεβαμμένον αἵματι, καὶ κέκληται τὸ ὄνομα αὐτοῦ ὁ λόγος τοῦ θεοῦ.<br />   <u>14</u>Καὶ τὰ στρατεύματα τὰ ἐν τῷ οὐρανῷ ἠκολούθει αὐτῷ ἐφʼ ἵπποις λευκοῖς, ἐνδεδυμένοι βύσσινον λευκὸν καθαρόν. <u>15</u>καὶ ἐκ τοῦ στόματος αὐτοῦ ἐκπορεύεται ῥομφαία ὀξεῖα, ἵνα ἐν αὐτῇ πατάξῃ τὰ ἔθνη, καὶ αὐτὸς ποιμανεῖ αὐτοὺς ἐν ῥάβδῳ σιδηρᾷ, καὶ αὐτὸς πατεῖ τὴν ληνὸν τοῦ οἴνου τοῦ θυμοῦ τῆς ὀργῆς τοῦ θεοῦ τοῦ παντοκράτορος, <u>16</u>καὶ ἔχει ἐπὶ τὸ ἱμάτιον καὶ ἐπὶ τὸν μηρὸν αὐτοῦ ὄνομα γεγραμμένον· Βασιλεὺς βασιλέων καὶ κύριος κυρίων.<br />   <u>17</u>Καὶ εἶδον ἕνα ἄγγελον ἑστῶτα ἐν τῷ ἡλίῳ καὶ ἔκραξεν ἐν φωνῇ μεγάλῃ λέγων πᾶσιν τοῖς ὀρνέοις τοῖς πετομένοις ἐν μεσουρανήματι·</p><h1>AN ANGEL IN THE SUN</h1><h4>Δεῦτε συνάχθητε εἰς τὸ δεῖπνον τὸ μέγα τοῦ θεοῦ <u>18</u>ἵνα φάγητε σάρκας βασιλέων καὶ σάρκας χιλιάρχων καὶ σάρκας ἰσχυρῶν καὶ σάρκας ἵππων καὶ τῶν καθημένων ἐπʼ αὐτῶν καὶ σάρκας πάντων ἐλευθέρων τε καὶ δούλων καὶ μικρῶν καὶ μεγάλων.</h4><p>   <u>19</u>Καὶ εἶδον τὸ θηρίον καὶ τοὺς βασιλεῖς τῆς γῆς καὶ τὰ στρατεύματα αὐτῶν συνηγμένα ποιῆσαι τὸν πόλεμον μετὰ τοῦ καθημένου ἐπὶ τοῦ ἵππου καὶ μετὰ τοῦ στρατεύματος αὐτοῦ. <u>20</u>καὶ ἐπιάσθη τὸ θηρίον καὶ μετʼ αὐτοῦ ὁ ψευδοπροφήτης ὁ ποιήσας τὰ σημεῖα ἐνώπιον αὐτοῦ, ἐν οἷς ἐπλάνησεν τοὺς λαβόντας τὸ χάραγμα τοῦ θηρίου καὶ τοὺς προσκυνοῦντας τῇ εἰκόνι αὐτοῦ· ζῶντες ἐβλήθησαν οἱ δύο εἰς τὴν λίμνην τοῦ πυρὸς τῆς καιομένης ἐν θείῳ. <u>21</u>καὶ οἱ λοιποὶ ἀπεκτάνθησαν ἐν τῇ ῥομφαίᾳ τοῦ καθημένου ἐπὶ τοῦ ἵππου τῇ ἐξελθούσῃ ἐκ τοῦ στόματος αὐτοῦ, καὶ πάντα τὰ ὄρνεα ἐχορτάσθησαν ἐκ τῶν σαρκῶν αὐτῶν.<br />   <strong>20</strong><u>1</u>Καὶ εἶδον ἄγγελον καταβαίνοντα ἐκ τοῦ οὐρανοῦ ἔχοντα τὴν κλεῖν τῆς ἀβύσσου καὶ ἅλυσιν μεγάλην ἐπὶ τὴν χεῖρα αὐτοῦ. <u>2</u>καὶ ἐκράτησεν τὸν δράκοντα, ὁ ὄφις ὁ ἀρχαῖος, ὅς ἐστιν Διάβολος καὶ ὁ Σατανᾶς, καὶ ἔδησεν αὐτὸν χίλια ἔτη <u>3</u>καὶ ἔβαλεν αὐτὸν εἰς τὴν ἄβυσσον καὶ ἔκλεισεν καὶ ἐσφράγισεν ἐπάνω αὐτοῦ, ἵνα μὴ πλανήσῃ ἔτι τὰ ἔθνη ἄχρι τελεσθῇ τὰ χίλια ἔτη. μετὰ ταῦτα δεῖ λυθῆναι αὐτὸν μικρὸν χρόνον.</p>","path":"/430","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you see any comparisons made by the author of this passage? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you see, describe any contrasts made by the author of this passage? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have noted in this passage.","Question4":"Please take time to tell a friend what God is teaching you through this reflection on the Bible.","prevPath":"/429","nextPath":"/431"}},"pathContext":{}}

